import vuePagination from './pagination.vue' // 
const pagination = {
  install (Vue, options) {
    Vue.component(vuePagination.name, vuePagination) // vuePagination.name name //  this.$xxx   // Vue.prototype.$xxx //  this.$xxx  // $ 
  }
}
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(pagination);
}
export default pagination // ..



